.login-form{
    width: 400px;
    position: relative;
    left: 35%;
    height: auto;
    top: 100px;
    border: 1px solid white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 1px;
    background-color: white;
}

.text-center{
    margin-left: -12px;
}

.modal{
    background-color: white;
    display:flex;
    position: relative;
    justify-content: center;
    /* align-items: center; */
    /* flex-direction: column; */
    /* position: relative; */
}

.modal-bg{
    z-index: 1;
    position:fixed;
    left: 0;
    top:0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition-property: visibility, opacity;
    transition-duration: 250ms;
    transition-timing-function: ease-in-out;
}
.active{
    visibility: visible;
    opacity: 1;
}

.close-icon{
    position: absolute;
    left: 90%;
    border-radius: 5px;
}

.close-icon:hover{
    background-color: rgb(228, 228, 228);
}

.invalid{
    /* border: 1px solid red; */
    color: red
}

.fas{
    position:absolute;
    left:0;
}
